import React, {useEffect} from 'react'
import { useSelector } from 'react-redux'
import { store } from '../shared/store/slices/store';
import "./scss/galary.scss"
import ProductCard from "../shared/componets/ProdactCard"
import { Link, useLocation } from 'react-router-dom'
import {cardsSlice, getCards} from "../shared/store/slices/cards"

export default function GallaryPage() {
  const location = useLocation()
  const cards = useSelector((state)=>state.cardsSlice)
  useEffect(() => {
      window.scrollTo(0, 0)
  }, [location]) 
  function filter(role) {
    return cards.items.filter((item, index) => item.role == role && <ProductCard key={index} item={item}/> )
  }
  console.log(filter('').length);
  useEffect(()=>{
    store.dispatch(getCards({}))
  }, [])
  return (
    <main>   
        <div className="gallary">
        {document.documentElement.clientWidth > 630 && <span><Link to="/"> </Link>/<Link to="/gallary">  </Link></span>}
            {filter('  ').length > 0 && <>
              <h1>  </h1>
              <div className="gallary__item">
                {cards.items.map((item, index) => item.role == '  ' && <ProductCard key={index} item={item}/> )}
              </div>
            </>}
            {filter(' ').length > 0 && <>
              <h1> </h1>
              <div className="gallary__item">
                {cards.items.map((item, index) => item.role == ' ' && <ProductCard key={index} item={item}/> )}
              </div>
            </>}
           {filter(' ').length > 0 && <>
            <h1> </h1>
            <div className="gallary__item">
              {cards.items.map((item, index) => item.role == ' ' && <ProductCard key={index} item={item}/> )}
            </div>
           </>}
           {filter('  ').length > 0 && <>
           <h1>  </h1>
            <div className="gallary__item">
            {cards.items.map((item, index) => item.role == '  ' && <ProductCard key={index} item={item}/> )}
            </div>
           </>}
          
            {filter('  ').length > 0 && <>
              <h1>  </h1>
              <div className="gallary__item">
              {cards.items.map((item, index) => item.role == '  ' && <ProductCard key={index} item={item}/> )}
              </div>
            </>}
            {filter('  ').length == 0 &&  filter('  ').length == 0 && filter(' ').length == 0 && filter(' ').length == 0 && filter('  ').length == 0 && <div className="bag__clear">
              <h2>   </h2>
              <p>  <Link to="/katalog"></Link>    </p>
              </div>}
          
        </div>
    </main>
  )
}
