import React, { useState,useEffect } from 'react'
import "./scss/katalog.scss"
import { useSelector } from 'react-redux'
import ProductCard from "../shared/componets/ProdactCard"
import { Link, useLocation } from 'react-router-dom'
import {cardsSlice, getCards} from "../shared/store/slices/cards"
import { store } from '../shared/store/slices/store';
import LoadingPage from './LoadingPage'

export default function KatalogPage() {
    const location = useLocation()
    const cards = useSelector((state)=>state.cardsSlice)
    console.log(cards);
    const [slider, setSlider] = useState({
        sliderOne: 10,
        sliderTwo: 100000
    })
    const [content, setContent] = useState({
        min: 1,
        max: 100
    })
    const [starsChecked, setStarsChecked] = useState({
        "": false,
        "": false,
        "": false,
        "": false
    })
    const [nameChecked ,setNameChecked] = useState({
        " ": false,
        "  ": false,
        " c ": false,
        " ": false,
        "  ": false,
        "": false
    })
    const [sizeChecked ,setSizeChecked] = useState({
        "62,42,50": false,
        "2,52,53,0": false,
        "62,42,4": false
    })
    const [showSideBar, setShowSideBar] = useState(false)
    const [cardsState, setCardsState] = useState([])
    const [showFirstFillter, setShowFirstFillter] = useState({
        state: false,
        active: ""
    })
    function allCheckboxFalse(object) {
        let flag = true;
        Object.values(object).forEach(x=>{
            if (x) {flag = false}})
        return flag
    }

    function checkSliderOne(e) {
        if (e.target.value < slider.sliderTwo) {
            setSlider(prevInput => ({...prevInput, sliderOne: e.target.value}))
        }
    }
    function checkSliderTwo(e) {
        if (e.target.value > slider.sliderOne) {
            setSlider(prevInput => ({...prevInput, sliderTwo: e.target.value}))
        }
    }
    function filterData() {
        const filterMass = cards.items.filter(item => {
            let trueItem = true;
       
            trueItem = trueItem &&  (slider.sliderOne <= item.price && slider.sliderTwo >= item.price ? true : false)
            trueItem = trueItem &&  (content.min <= item.content && content.max >= item.content ? true : false)
     
            if (!allCheckboxFalse(sizeChecked)) {
                trueItem = trueItem &&  (sizeChecked[item.size] ? true : false)
            }
            if (!allCheckboxFalse(starsChecked)) {
                trueItem =  trueItem && (starsChecked[item.star] ? true : false)
            }
            
            if (!allCheckboxFalse(nameChecked)) {
                trueItem = trueItem && (nameChecked[item.role] ? true : false)
            } else{
                trueItem = trueItem  && (item.role == '' ? true : false)
            }
           
            return trueItem
        })
        setCardsState(filterMass)
        return showFirstFillter.active == "" ?  filterMass.sort(function (a, b) {return a.content - b.content;}) : showFirstFillter.active == "" ? filterMass.sort(function (a, b) {return a.price - b.price;}) : showFirstFillter.active == "" &&  filterMass.sort(function (a, b) {return b.price - a.price;})
    }

    function clear() {
        for (const key in starsChecked) {setStarsChecked(starsChecked[key] = false)}
        for (const key in sizeChecked) {setSizeChecked(sizeChecked[key] = false)}
        for (const key in nameChecked) {setNameChecked(nameChecked[key] = false)}
        setContent({min: 1, max: 10})
        setSlider({sliderOne: 1000, sliderTwo: 20000})
    }
    function ShowFillter() {
        return (
        <div className="katalog__filter">
            <div className="katalog-row">
                <h2></h2>
                <p className="filter__close" onClick={()=>setShowSideBar(!showSideBar)} >x</p>
            </div>
            <div className="katalog-row">
                <label className="label__price">  <p>{slider.sliderOne}</p> </label>
                <label className="label__price">  <p>{slider.sliderTwo}</p></label>
            </div>
            <div className="container">
                <div className="slider-track"  style={{background: `linear-gradient(to right, #dadae5 ${(slider.sliderOne / 100000) * 100}% ,#5134c4 ${ (slider.sliderOne / 100000) * 100}% ,#b856d4 ${(slider.sliderTwo / 100000) * 100}%, #dadae5 ${(slider.sliderTwo / 100000) * 100}%)`}}></div>
                <label><input type="range" min="0" max="100000" value={slider.sliderOne} id="slider-1" onChange={(e)=>checkSliderOne(e)}/></label>
                <label><input type="range" min="10" max="100000" value={slider.sliderTwo} id="slider-2" onChange={(e)=>checkSliderTwo(e)}/></label>
            </div>
            <div className="katalog-name">
                <h2></h2>
                <label> <input type="checkbox" onClick={()=>setNameChecked({...nameChecked,  " ": !nameChecked[" "]})} checked={nameChecked[" "]}/> </label>
                <label> <input type="checkbox" onClick={()=>setNameChecked({...nameChecked,  "  ": !nameChecked["  "]})} checked={nameChecked["  "]}/>  </label>
                <label> <input type="checkbox" onClick={()=>setNameChecked({...nameChecked,  " c ": !nameChecked[" c "]})} checked={nameChecked[" c "]}/> c </label>
                <label> <input type="checkbox" onClick={()=>setNameChecked({...nameChecked,  "  ": !nameChecked["  "]})} checked={nameChecked["  "]}/>  </label>
                <label> <input type="checkbox" onClick={()=>setNameChecked({...nameChecked,  " ": !nameChecked[" "]})} checked={nameChecked[" "]}/> </label>
                <label> <input type="checkbox" onClick={()=>setNameChecked({...nameChecked,  "": !nameChecked[""]})} checked={nameChecked[""]}/></label>
            </div>
            <div className="katalog-size">
                <h2></h2>
                <label><input type="checkbox" onClick={()=>setSizeChecked({...sizeChecked,  "62,42,50": !sizeChecked["62,42,50"]})} checked={sizeChecked["62,42,50"]}/>62,42,50</label>
                <label><input type="checkbox" onClick={()=>setSizeChecked({...sizeChecked,  "2,52,53,0": !sizeChecked["2,52,53,0"]})} checked={sizeChecked["2,52,53,0"]}/>2,52,53,0</label>
                <label><input type="checkbox" onClick={()=>setSizeChecked({...sizeChecked,  "62,42,4": !sizeChecked["62,42,4"]})} checked={sizeChecked["62,42,4"]}/>62,42,4</label>
            </div>
                <h2></h2>
                <div className="katalog-row">
                    <label className="label__price">  <input type="text" maxLength="2" value={content.min} onChange={(e)=>setContent({...content, min: e.target.value})} className="katalog__price" /></label>
                    <label className="label__price">  <input type="text" maxLength="3" value={content.max} onChange={(e)=>setContent({...content, max: e.target.value})}  className="katalog__price"/></label>
                </div>
            <div className="katalog-star">
                <h2> </h2>
                <label> <input type="checkbox" onClick={()=>setStarsChecked({...starsChecked,  "": !starsChecked[""]})} checked={starsChecked[""]}/></label>
                <label> <input type="checkbox" onClick={()=>setStarsChecked({...starsChecked,  "": !starsChecked[""]})} checked={starsChecked[""]}/></label>
                <label> <input type="checkbox" onClick={()=>setStarsChecked({...starsChecked,  "": !starsChecked[""]})} checked={starsChecked[""]}/></label>
                <label> <input type="checkbox" onClick={()=>setStarsChecked({...starsChecked,  "": !starsChecked[""]})} checked={starsChecked[""]}/></label>
            </div>
            <button id="clear" onClick={clear}></button>
        </div>
        )
     }

    useEffect(()=>{
        store.dispatch(getCards({}))
    }, [])
    useEffect(()=>{
        cards.items.length != 0 && filterData()
    }, [sizeChecked,starsChecked, nameChecked, content, slider, showFirstFillter.state , cards.items])
    useEffect(() => {
        window.scrollTo(0, 0)
    }, [location])

    return (
        <main>
            <div className="katalog">
            {document.documentElement.clientWidth > 630 && <>
                <span><Link to="/"> </Link>/<Link to="/katalog"> </Link></span>
                <h1></h1>
            </>
            }
                <div className="katalog-row">
                        <p className='firstFilter'>:   
                            <div id="firstFilter">
                            <p className='active' onClick={()=>setShowFirstFillter({...showFirstFillter, state: true})}>{showFirstFillter.active}</p> 
                            {showFirstFillter.state && <div className='select'>
                                <p onClick={()=>setShowFirstFillter({active: "", state: false})}></p>
                                <p onClick={()=>setShowFirstFillter({active: "", state: false})}></p>
                                <p onClick={()=>setShowFirstFillter({active: "", state: false})}></p>
                            </div>}    
                            </div>
                        </p>
                        {document.documentElement.clientWidth > 630 &&  <label className="katalog__kol"> : <span id="kolOnPage">{cardsState.length}</span></label>}
                        <div className="filter__open" onClick={()=>setShowSideBar(!showSideBar)}><img src="./filter.webp" alt=""/><p></p></div>
                </div>
                <div className="katalog-row">
                    {document.documentElement.clientWidth > 1024 && ShowFillter()}
                    {document.documentElement.clientWidth < 1024 && showSideBar && ShowFillter()}
                    <div className="katalog-line">
                        {cards.isLoading ? <LoadingPage number={6}/> : cardsState.length == 0 ?  <div className="bag__clear"><h2>     </h2><p>     </p></div> : cardsState.map((item, index) => <ProductCard key={index} item={item}/>)}
                    </div>
                </div>
            </div>
        </main>
    )
}