import React, { useState } from 'react'
import { Link, useLocation, useNavigate} from 'react-router-dom';
import { useSelector } from 'react-redux'
import "./css/footer.scss"
import { authSlice } from '../shared/store/slices/auth';
import {BagSlice, checkRegister} from "../shared/store/slices/bag"
import {store} from "../shared/store/slices/store"
export default function Footer() {
    const auth = useSelector((state)=>state.authSlice)
    const BagStore = useSelector((state) => state.BagSlice);
    const location = useLocation()
    const navigate = useNavigate()
    let newBagMass = BagStore.items.filter(item=>item.data.have == 'true')
    const [massQuestion, setMassQuestion] = useState([
        {
            quastion: " ?",
            answer: ",    ,   ",
            active: false
        },
        {
            quastion: "   ?",
            answer: " /,    , ,     .             .",
            active: false
        },
        {
            quastion: "   ?",
            answer: ",    ,   ",
            active: false
        },
        {
            quastion: "   ",
            answer: ",    ,   ",
            active: false
        },
        
    ])
    function showAnswer(item) {
        let newMass = massQuestion.slice()
        newMass.forEach((card)=>{
            if (card.quastion == item.quastion) {
                    card.active =  !card.active
            }
        })
        setMassQuestion(newMass)
    } 
    function BagStorePrice() {
        let summ = 0;
        newBagMass.forEach(item=>{
            summ+= (item.count*item.data.price )*item.month 
        })
        return summ
    }
    function BagStoredisCount() {
        let summ = 0;
        newBagMass.forEach(item=>{
            summ+= (item.count*item.data.discount )*item.month 
        })
        return summ
    }
    function send() {
        if (!auth.userData) {
            store.dispatch(BagSlice.actions.checkRegister(" :    "));
        } else {
            if (BagStoredisCount() == 0) {
                store.dispatch(BagSlice.actions.checkRegister(" :       "));
            } else{
                navigate(`/arrange`)
            }
        }
    }
  return (
   document.documentElement.clientWidth > 630 ? 
    <footer>
    <div className="pd-100">
        <h2>  </h2>
       {massQuestion.map((item, index)=> 
       <div key={index}  className="answer" >
            <div className="answer__block">
                <div className="answer__question">
                    <div className="answer__info">i</div>
                    <p>{item.quastion}</p>
                    <p className={`answer__open  ${item.active ? "active" : ""}`} onClick={()=>showAnswer(item)}>{">"}</p>
                </div>
                <div className={`answer__content ${item.active ? "active" : ""}`}>{item.answer}</div>
            </div>
        </div>
        )}
    </div>
    <div className="basement">
        <div className="basement__block">
            <div className="basement__col">
                <h2>,   ! <br/>   </h2>
                <p>    ,    !</p>
            </div>
            <div className="basement__col">
                <h3>   1249 !</h3>
                <div className="logFooter">
                    { auth.userData == null ? 
                    <>
                        <Link to="/login" className="login"></Link>
                        <Link to="/register" className="register"></Link>
                    </> :
                     <Link to="/katalog" className="katalog"></Link>
                }
                   
                </div>
                <h3>!</h3>
            </div>
        </div>
        <div className="basement__content">
            <div className="d-col">
                <Link to="/" className="basement__logo">MSB <span>container</span></Link>
                <div className="block">
                    <Link to="#" className="prava">2022   </Link>
                    <Link to="#" className="prava"> </Link>
                    <Link to="#" className="prava"> </Link>
                    <Link to="/admin" className="prava"> </Link>
                </div>
            </div>
            <div className="d-col">
                <h4></h4>
                <Link to="/card/2">  </Link>
                <Link to="/card/1"> </Link>
                <Link to="/card/5"> </Link>
                <Link to="/card/4">  </Link>
                <Link to="/card/3">  </Link>
            </div>
            <div className="d-col">
                <h4></h4>
                <Link to="/aboutUs"> </Link>
                <Link to="/contact"></Link>
                <Link to="/services"></Link>
                <Link to="/gallary"> </Link>
            </div>
            <div className="d-col">
                <h4></h4>
                <p>+7 (910)973-36-65</p>
                <p>	dir@ids76.ru</p>
                <p>. ,  2-  2</p>
            </div>
        </div>
    </div>
</footer> : <>
    {location.pathname.split("/")[1] == 'bag' && BagStore.items != 0 ? 
        <div className="mobile__bag">
            <p>{BagStorePrice()}</p>
            <p>{BagStoredisCount()}</p>
            <button id="order" to="/arrange" onClick={send}> </button>
        </div>
    : false} 
    <div className="mobile">
        <div className="mobile__block">
            <Link to='/'><img src={location.pathname.split("/")[1] == '' ? '/homeActive.svg' : '/home.svg'} alt=""/></Link>
            <p className={`mobile__text ${location.pathname.split("/")[1] == '' && 'active'}`}></p>
        </div>
        <div className="mobile__block">
            <Link to='/katalog'><img src={location.pathname.split("/")[1] == 'katalog' ? '/katalogActive__icon.svg' : '/katalog__icon.svg'} alt=""/></Link>
            <p className={`mobile__text ${location.pathname.split("/")[1] == 'katalog' && 'active'}`}></p>
        </div>
        <div className="mobile__block">
            <Link to='/gallary'><img src={location.pathname.split("/")[1] == 'gallary' ? '/productActive__icon.svg' : '/product__icon.svg'} alt=""/></Link>
            <p className={`mobile__text ${location.pathname.split("/")[1] == 'gallary' && 'active'}`}></p>
        </div>
        <div className="mobile__block">
            <Link to="/bag" ><img src={location.pathname.split("/")[1] == 'bag' ? '/bagActive__icon.svg' : '/bag__icon.svg'} alt=""/><div className="services__sum"><p>{BagStore.items?.length}</p></div></Link>
            <p className={`mobile__text ${location.pathname.split("/")[1] == 'bag' && 'active'}`}></p>
        </div>
        <div className="mobile__block">
            <Link to={auth?.userData == null ? '/register' : `/user/${auth?.userData?.idUser}`}><img src={location.pathname.split("/")[1] == 'user' || location.pathname.split("/")[1] == 'register' || location.pathname.split("/")[1] == 'login' ? '/userActive__icon.svg' : '/user__icon.svg'} alt=""/></Link>
            <p className={`mobile__text ${location.pathname.split("/")[1] == 'user' || location.pathname.split("/")[1] == 'register' || location.pathname.split("/")[1] == 'login'  ? 'active' : false}`}></p>
        </div>
    </div>
    </> 
  )
}
