import React, {useEffect} from 'react'
import { Link, useLocation } from 'react-router-dom'
import "./scss/services.scss"

export default function ServicesPage() {
    const location = useLocation()
    useEffect(() => {
        window.scrollTo(0, 0)
    }, [location]) 
  return (
    <main>   
        <div className="services__wrapper">
            <span><Link to="/"> </Link>/<Link to="/services"> </Link></span>      
            <h1></h1>
            <div className="services">
                <div className="services__item">
                    <div className="services__img">
                        <img src="https://backend.msb-container.ru/uploads/1-cubinsForLive.webp" alt=""/>
                    </div>
                    <div className="services__contant">
                        <div className="star"></div>
                        <p>   </p>
                        <div className="services__contantPrice">
                            <Link to="/" className="more"></Link>
                            <p> 8000</p>
                        </div>
                    </div>
                </div>
                <div className="services__item">
                    <div className="services__img">
                        <img src="./cubins.webp" alt=" /"/>
                    </div>
                    <div className="services__contant">
                        <div className="star"></div>
                        <p>  /</p>
                        <div className="services__contantPrice">
                            <Link to="/" className="more"></Link>
                            <p> 15000</p>
                        </div>
                    </div>
                </div>
                <div className="services__item">
                    <div className="services__img">
                        <img src="./transportiration.webp" alt=" "/>
                    </div>
                    <div className="services__contant">
                        <div className="star"></div>
                        <p>   </p>
                        <div className="services__contantPrice">
                            <Link to="/" className="more"></Link>
                            <p> 8000</p>
                        </div>
                    </div>
                </div>
                <div className="services__item">
                    <div className="services__img">
                        <img src="https://backend.msb-container.ru/uploads/5-cubinsForWork.webp" alt=""/>
                    </div>
                    <div className="services__contant">
                        <div className="star"></div>
                        <p> </p>
                        <div className="services__contantPrice">
                            <Link to="/katalog" className="more"></Link>
                            <p> 5000</p>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </main>
  )
}
