import React, {useEffect, useState} from 'react'
import "./scss/peoplePage.scss"
import { Link } from 'react-router-dom';
import {peopleSlice, getPeople } from "../shared/store/slices/people"
import { useSelector } from 'react-redux'
import { store } from '../shared/store/slices/store';
import PeopleCard from '../shared/componets/PeopleCard';

export default function PeoplePage() {
    const people =   useSelector((state)=>state.peopleSlice)
    const [peopleFilter, setPeopleFilter] = useState("all")
    useEffect(()=>{
        store.dispatch(getPeople({}))
    }, [])
  return (
    <div>
        <div className='headerOrder'>
            <Link to="/admin/createPeople" > </Link>
            <a onClick={()=>setPeopleFilter("")} ></a>
            <a onClick={()=>setPeopleFilter("")} ></a>
            <a onClick={()=>setPeopleFilter("all")} className='headerOrder__margin'> </a>
        </div>
        <h1></h1>
        <div className="people">
            {peopleFilter != "" && <>
            <h2></h2>
            <div className="people__order">
                { people?.items?.find(item=>item.role == "") != undefined  ?  people?.items.map((item, index)=>item.role == "" && <PeopleCard key={index} item={item}/>) :  <div className="order__clear">
                    <h2>  </h2>
                    <p><Link to="/admin/createPeople" > </Link>    </p>
                </div>}
            </div>
            </> }
           {peopleFilter != "" && <>
           <h2></h2>
            <div className="people__order">
                {people?.items?.find(item=>item.role == "") != undefined  &&  peopleFilter != "" ?  people?.items.map((item, index)=>item.role == "" && <PeopleCard key={index} item={item}/>) :  <div className="order__clear">
                    <h2>  </h2>
                    <p><Link to="/admin/createPeople" > </Link>    </p>
                </div>}
            </div>
           </>} 
        </div>
    </div>
  )
}
