import React, { useEffect, useState }  from 'react'
import { Link } from 'react-router-dom';

export default function OrdersCard({item, orderFilter}) {
  return (
    <>  
      <div className="bag__block">
            <img className='card__img' src={item.img} alt={`  ${item.number}`}/>
            <div className="block__content"> 
            {!item.orderCollect && !item.orderAccepted && !item.orderGo && !item.orderReceived ? <p className="block__orderNew"></p> : item.orderCollect && item.orderAccepted && item.orderGo && item.orderReceived ? <p className="block__orderTrack"></p> : <p className="block__orderAccept"></p>}
                <p className="orderNumber">  {item.number}</p>
                <p className='rent'>C : <span>{item.delivery == "" ? `:` : ""}</span></p>
                <Link className="track" to={`/admin/order/${item.idUser}/${item.number}`}>{!item.orderReceived ? " " : " " }</Link>
            </div>
      </div>
    </>
  )
}
